import { Component, OnInit, TemplateRef } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ToastrService } from 'ngx-toastr';

import { OwnersService } from './owners.service';
import { StoresService } from '../stores/stores.service';
import { InputSelectItem } from 'src/app/models/InputSelectItem';

@Component({
  selector: 'app-owners',
  templateUrl: './owners.component.html',
  styleUrls: ['./owners.component.scss'],
})
export class OwnersComponent implements OnInit {
  items = [];
  modalRef: any;
  constructor(
    private modalService: BsModalService,
    private toastr: ToastrService,
    private ownerService: OwnersService,
    private storesService: StoresService
  ) {}

  ngOnInit(): void {
    this.ownerService.getAll();
    this.ownerService.owners.subscribe((data) => {
      if (data) {
        console.log(data);
        this.items = data;
      }
    });
  }

  // Start select dropdown
  selectStoresList: InputSelectItem[];
  selectedStoreId: string;
  getStoresList() {
    this.storesService.getAll();

    this.storesService.stores.subscribe((data) => {
      if (data) {
        this.selectStoresList = data.map((x) => {
          return {
            _id: x['_id'],
            nameEn: `${x['name']}`,
            nameAr: ``,
          };
        });
      }
    });
  }
  selectStore(event) {
    if (event) {
      this.selectedStoreId = event._id;
    }
  }
  // End select dropdown

  reloadItems($event) {}
  FilterRecords(FilterRecord) {}

  itemId;
  openModal(template: TemplateRef<any>, id) {
    this.itemId = id;
    this.modalRef = this.modalService.show(template);
  }

  addOwner() {
    this.modalRef.hide();
    this.ownerService.addOwner(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.ownerService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }

  deleteOwner() {
    this.modalRef.hide();
    this.ownerService.deleteOwner(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.ownerService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }

  addOwnerToStore() {
    this.modalRef.hide();
    this.ownerService
      .addOwnerToStore(this.itemId, this.selectedStoreId)
      .subscribe(
        (res) => {
          console.log(res);
          this.toastr.info(res['message'], 'Info');
          this.items = [];

          this.ownerService.getAll();
        },
        (error) => {
          console.log(error);
          this.toastr.error(error.error.message, 'Error');
          // this.error.next(error.error.message);
        }
      );
  }

  lockElement() {
    this.modalRef.hide();
    this.ownerService.lockOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.ownerService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }

  unlockElement() {
    this.modalRef.hide();
    this.ownerService.unlockOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.ownerService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }
}
